@value makerWheat,makerBlack, makerWhite, makerColorful, makerShadow, makerWheat  from '../../../../common/colors.css';

.card{
 display: flex;
 align-items: center;
 width: 100%;
 background-color: yellow;
 margin: 1.5em;
 border-radius: 1em;
 box-shadow: 3px 3px 6px 6px rgba(217, 217, 217,1);
 max-width: 30rem;
}

.card.dark {
  background-color: makerBlack;
  color: makerWhite;
}

.card.light {
  background-color: makerWhite;
  color: makerBlack;
}
.card.colorful {
  background: makerColorful;
}

.avatar{
  width: 10em;
  height: 10em;
  padding: 1em;
  margin-left: 0.5em;
  margin-top: 1em;
  border-radius: 50%;
}

.name,
.company,
.title,
.email,
.message {
  margin: 0;
  font-size: 0.8rem;
  margin-bottom: 0.2em;
}

.name{
  font-size: 1.2rem;
}

.company{
  margin-bottom: 1em;
}

.info{
  width: 100%;
}

.avatar {
  width: 10em;
  height: 10em;
  padding: 1em;
  margin-left: 0.5em;
  margin-right: 1em;
  border-radius: 50%;
}

.company::after{
  content: '';
  display: block;
  width: 90%;
  height: 2px;
  background-color: makerWheat;
  transform: translateY(0.5em);
}

